.list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.list--li {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: 3%;
  margin-bottom: 0.25rem;
}

@media print {
  .list--li {
    page-break-inside: avoid;
  }
}

.list--li::before {
  content: none;
  display: none;
}

.list--col_0 {
  display: block;
  grid-column-start: 1;
  grid-column-end: 5;
}

.list--col_0--inner {
  margin-top: 0.3em;
  font-size: 0.85em;
  line-height: 1.3;
  color: #004b5f;
  font-weight: 700;
  display: block;
}

.list--col_1 {
  display: block;
  grid-column-start: 5;
  grid-column-end: 13;
}

@media screen and (min-width: 630px) {
  .list--col_0 {
    grid-column-end: 4;
  }
  .list--col_1 {
    grid-column-start: 4;
  }
}

.list--col_1--level {
  transition: width 350ms;
}

.skills {
  position: -webkit-sticky;
  position: sticky;
  top: 3rem;
  background-color: white;
  padding-top: 0.5rem;
  padding-bottom: 0.45rem;
}

@media print {
  .skills {
    position: relative;
    top: 0;
  }
}

.skills + li {
  margin-top: -0.45rem;
}

.skills--basic,
.skills--pro,
.skills--advanced {
  display: block;
  position: relative;
  height: 0;

  grid-column-start: 5;
  grid-column-end: 13;
  font-size: 0.7rem;
}

@media screen and (min-width: 630px) {
  .skills--basic,
  .skills--pro,
  .skills--advanced {
    grid-column-start: 4;
  }
}

.skills--basic::after,
.skills--pro::after,
.skills--advanced::after {
  content: "";
  width: 1px;
  height: 0.9rem;
  background-color: #004b5f;
  display: block;
  margin-top: 0.45rem;
}

.skills--basic::after {
}
.skills--pro::after {
  margin-left: calc(50% - 0.5px);
}
.skills--advanced::after {
  margin-left: calc(100% - 0.5px);
}

.skills--basic {
  margin-top: 0.6rem;
}

.skills--pro {
  text-align: center;
}

.skills--advanced {
  text-align: right;
}

.skills--label {
  display: block;
  position: absolute;
  width: 100%;
  margin-top: -0.9rem;
}

.avatar {
  margin-top: 1rem;
}

.avatar--img {
  width: 100%;
  height: auto;
  display: block;
}

.avatar--icon {
  width: 40%;
  height: auto;
  display: block;
}

